.App {
  text-align: center;
}

h1 {
  /* color: #fff; */
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}

.TodoWrapper {
  /* background: #1A1A40; */
  margin-top: 5rem;
  /* padding: 2rem; */
  border-radius: 10px;
}

.TodoForm {
  width: 100%;
  border-radius: 10px;
}

.todo-input {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  background: none;
  border: 1px solid #61c8fb;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: black;
}

.todo-input::placeholder {
  color: #ffffff4d;
}

.todo-btn {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background: #61c8fb;
  color: #fff;
  border: none;
  padding: 0.55rem;
  cursor: pointer;
  
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #61c8fb;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin-bottom: 1rem;
  cursor: pointer;
  width: 300px;
  height: 40px;
}

.fa-trash {
  margin-left: 0.75rem;
}

